.past-clients {
  min-height: 500px;
  padding-top: 100px;

}

.container-past {
  @include flex-column-center;
}


.past-title {
  padding-bottom: 15px;
  padding-top: 10px;
  font-size: 40px;
  line-height: 42px;
  text-align: center;
  color: var(--color-white);
}

.past-descr {
  padding-bottom: 62px;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
  color: var(--color-gray);
}


.services-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 109px;

  padding-top: 20px;


  &__item {
    width: 128px;
    height: 78px;
    padding: 5px 5px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;


    &--p1 {
      background-image: url(/img/svg/p1.svg);
    }

    &--p2 {
      background-image: url(/img/svg/p2.svg);
    }

    &--p3 {
      background-image: url(/img/svg/p3.svg);
    }

    &--p4 {
      background-image: url(/img/svg/p4.svg);
    }

    &--p5 {
      background-image: url(/img/svg/p5.svg);
    }
  }
}