@mixin flex {
  display: flex;
}

@mixin flex-v-center {
  display: flex;
  align-items: center;
}

@mixin flex-h-center {
  display: flex;
  justify-content: center;
}

@mixin flex-all-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin flex-all-sb {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin flex-column-start {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@mixin flex-column-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin flex-column-end {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

}

@mixin flex-space-b {
  display: flex;
  justify-content: space-between;
}

@mixin flex-jc-end {
  display: flex;
  justify-content: flex-end;
}

@mixin flex-space-end {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

@mixin flex-h-end {
  display: flex;
  align-self: flex-end;
}

@mixin flex-column-jc-end {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

@mixin flex-end-end {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}