.gear-cage {

  // padding-top: 220px;
  // padding-bottom: 20px;
  height: 540px;
  background-color: var(--color-black-bg);
  // background-image: url(../img/hero.jpeg);
  // background-position: center;
  // background-size: cover;
  // background-repeat: no-repeat;
  // color: var(--color-light);

  .container {

    @include flex-space-b;

    min-height: 540px;
    min-width: 1470px;
    padding-left: 0px;


    .gear-container {
      width: 50%;

      .gear-info {
        margin: 0;
        min-width: 515px;
        padding: 173px 106px 153px 100px;

        &__title {
          margin-bottom: 6px;
          font-size: 14px;
          line-height: 20px;
          letter-spacing: 2px;
          text-transform: uppercase;
          color: var(--color-gray);

          &--big {
            margin-bottom: 14px;
            font-size: 28px;
            line-height: 32px;
            color: var(--color-white);
          }
        }

        &__descr {
          margin-bottom: 20px;
          font-size: 14px;
          line-height: 20px;
          color: var(--color-white);
        }
      }
    }


    .info-container {

      @include flex-end-end;

      width: 50%;
      background-image: url(../img/gear.jpg);
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      color: var(--color-light);
    }
  }
}