.gallery-container {
  background-color: var(--color-black-bg);
  padding-top: 120px;
  padding-bottom: 10px;

  .text-wrapper {

    &__title {
      padding-bottom: 15px;
      padding-top: 10px;
      font-size: 40px;
      line-height: 42px;
      text-align: center;
      color: var(--color-white);
    }

    &__descr {

      padding-bottom: 42px;
      font-size: 23px;
      line-height: 30px;
      text-align: center;
      color: var(--color-gray);


    }
  }

  /////////////////////////////////////

  .gallery {

    @include flex-h-center;
    display: flex;
    flex-wrap: wrap;




    &__item {
      @include flex-column-jc-end;

      margin-bottom: 16px;
      margin-left: 8px;
      margin-right: 8px;
      padding-left: 30px;
      padding-right: 32px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      width: 576px;
      height: 600px;
      padding-bottom: 30px;

      &--title {
        margin-bottom: 5px;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: var(--color-white);
      }

      &--descr {
        margin-bottom: 20px;
        font-size: 14px;
        line-height: 20px;
        color: var(--color-gray);
      }


      &--product {
        background-image: url(/img/gallery/g-1.jpg);
      }

      &--architecture {
        background-image: url(/img/gallery/g-2.jpg);
      }

      &--drone {
        background-image: url(/img/gallery/g-3.jpg);
      }

      &--wildlife {
        background-image: url(/img/gallery/g-4.jpg);
      }


      // &__title {
      //   @include main-text-title;
      // }

      // &__descr {
      //   @include main-text-descr;
      // }



    }
  }
}