// галерея кнопки
.primary-btn {
  @include flex-all-sb;

  font-size: 15px;
  line-height: 20px;
  padding-left: 4px;
  padding-right: 20px;

  width: 144px;
  height: 42px;
  border-radius: 14px;
  color: var(--color-black-bg);
  background-color: var(--color-white);
}

.primary-btn__square {
  border-radius: 26px;
  background-color: var(--color-black-bg);
  width: 31px;
  height: 31px;
}


// -----    // -----    // -----    // -----    // -----   Aperture section // -----    // -----    // -----    // -----    // -----

.info {
  margin: 0;

  @include flex-h-end;
  margin-bottom: 35px;

  &__list {
    @include flex-space-b;
    padding-right: 20px;
  }

  &__item {
    padding: 5px 5px;
    background-size: 16px 16px;
    background-repeat: no-repeat;
    background-position: left;

    &:not(:last-child) {
      margin-right: 16px;
    }

    &--first {
      background-image: url(/img/svg/bg-it1.svg);
      padding-left: 8px;
    }

    &--second {
      padding-left: 8px;
      background-image: url(/img/svg/bg-it2.svg);
    }

    &--third {
      background-size: 32px 16px;
      padding-left: 28px;
      background-image: url(/img/svg/bg-it3.svg);
    }

    &--fourth {
      background-image: url(/img/svg/bg-it4.svg);
    }

    &--title {
      @include aperture-info-text;
      padding-left: 12px;

    }
  }
}