.third-bg {
  @include flex-space-end;

  margin: 0;
  min-height: 600px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(../img/third-bg.jpg);
  background-color: var(--color-black-bg);
  color: var(--color-light);



  .container {

    @include flex-column-end;
    margin: 0;
    min-width: 100%;


    .content-section {
      max-width: 345px;
      padding-bottom: 202px;
      padding-right: 25px;


      &__title {
        text-align: start;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: var(--color-white);

      }

      &__descr {
        @include main-text-descr;
        text-align: start;
      }
    }
  }
}