.footer {

  @include flex-all-center;
  // height: 652px;
  // width: 1168px;
  // border: 2px solid red;
}

.footer-container {
  height: 652px;
  width: 1168px;
  padding-top: 100px;

}

.top-footer {
  width: 100%;
  // @include flex-all-sb;
  display: flex;
  justify-content: space-between;
}

.logo-section {
  width: 50%;
  max-width: 440px;
  padding-top: 10px;

  &__logo {
    background-repeat: no-repeat;
    width: 128px;
    height: 31px;
    background-image: url(/img/logo_aperture.svg);
  }

  &__text {
    margin-top: 20px;
    font-size: 13px;
    line-height: 20px;
    color: var(--color-gray);
  }
}

//////////////////////////////

.nav-section {
  // margin-left: 240px;
  display: flex;
  justify-content: center;
  // max-width: 190px;


  &__list {
    @include flex-column-start;



    padding-right: 55px;

  }


  &__item {
    padding-top: 10px;
    max-width: 182px;
  }


  &__link {
    font-size: 14px;
    line-height: 20px;
    color: var(--color-white);

    &--other {
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--color-gray);
    }
  }
}

//////////////////////////////////////////////////////////////////////////////////////////2

.middle-section {
  @include flex-space-b;
  margin-top: 36px;
}

.left-section {

  &__title {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: var(--color-white);
  }

  &__subtitle {
    margin-top: 20px;
    font-size: 13px;
    line-height: 20px;
    color: var(--color-gray);
  }
}

.right-section {
  &__btn {
    display: flex;
    justify-content: flex-end;
    padding: 11px 6px 11px 0px;
    width: 458px;
    height: 52px;
    background: #F9FAFB;
    border: 1px solid #E5E7EB;
    border-radius: 12px;

    &--small {
      width: 63px;
      height: 30px;
      border-radius: 11px;
      background-color: var(--color-black-bg);

    }
  }
}



.middle-line {
  margin-top: 36px;
  width: 100%;
  height: 2px;
  background-color: var(--color-white);
}

.down-section {
  @include flex-space-b;
  margin-top: 56px;

  &__list {
    display: flex;
    justify-content: flex-end;
  }

  &__item {
    background-repeat: no-repeat;
    background-size: contain;
    width: 30px;
    height: 30px;
    margin-left: 20px;

    &--tw {
      background-image: url(/img/svg/tw.svg);
    }

    &--inst {
      background-image: url(/img/svg/inst.svg);
    }

    &--faceb {
      background-image: url(/img/svg/facebook.svg);
    }
  }
}