.hero {
  @include flex-space-end;
  // padding-top: 220px;
  // padding-bottom: 20px;
  height: 100vh;
  background-color: var(--color-gray);
  background-image: url(../img/hero.jpeg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: var(--color-light);


  .container {
    @include flex-column-center;
    margin: 0;
    min-width: 100%;


    .content-section {
      max-width: 600px;
      padding-bottom: 12px;

      &__title {
        @include main-text-title;
        text-align: center;

        &--big {

          padding-bottom: 18px;
          text-align: center;
          font-size: 54px;
          line-height: 59px;
          color: var(--color-white);
        }
      }

      &__descr {
        @include main-text-descr;
      }
    }

    // -----    // -----    // -----    // -----    // -----   Aperture section

    .info {
      @include flex-h-end;
      margin-bottom: 25px;

      &__list {
        @include flex-space-b;
        padding-right: 30px;
      }

      &__item {
        padding: 5px 5px;
        background-size: 16px 16px;
        background-repeat: no-repeat;
        background-position: left;

        &:not(:last-child) {
          margin-right: 16px;
        }

        &--first {
          background-image: url(/img/svg/bg-it1.svg);
          padding-left: 8px;
        }

        &--second {
          padding-left: 8px;
          background-image: url(/img/svg/bg-it2.svg);
        }

        &--third {
          background-size: 32px 16px;
          padding-left: 28px;
          background-image: url(/img/svg/bg-it3.svg);
        }

        &--fourth {
          background-image: url(/img/svg/bg-it4.svg);
        }

        &--title {
          @include aperture-info-text;
          padding-left: 12px;

        }
      }
    }
  }
}